import props from './computed';
import { addUnit } from '../common/utils/add-unit';
import PressAvatar from '../press-avatar/press-avatar.vue';
import styleUtil from '../common/utils/style';


export default {
  name: 'PressAvatarGroup',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  components: {
    PressAvatar,
  },
  mixins: [props],
  emits: [
    'showMore',
  ],
  data() {
    return {

    };
  },
  computed: {
    showUrl() {
      return this.urls.slice(0, this.maxCount);
    },
    extraText() {
      const { extraValue, urls, showUrl } = this;
      return extraValue || urls.length - showUrl.length;
    },
    textStyle() {
      return styleUtil({
        color: '#fff',
        fontSize: this.size * 0.4,
        textAlign: 'center',
      });
    },
  },
  methods: {
    addUnit,
    getAvatarUrl(item) {
      if (typeof item === 'object') {
        return item[this.keyName || 'url'];
      }
      return item;
    },
    clickHandler() {
      this.$emit('showMore');
    },
    getItemStyle(index) {
      const { size, gap } = this;
      const res =  styleUtil({
        marginLeft: index === 0 ? 0 : addUnit(-size * gap),
        width: addUnit(size),
        height: addUnit(size),
      });
      console.log('res', res);
      return res;
    },
  },
};
